      var tmpArr = [];
      while (tmpArr.length < 6) {
        console.log(1);
        tmpArr.push({
          name: lengedArr[this.getRndInteger(0, 15)],
          value: this.getRndInteger(min, max),
          textStyle: {
            color: "#fff"
          }
        });
        let uniqueArr = [...new Set(tmpArr)];
        tmpArr = uniqueArr;
      }
      seriesData = this.maopao(tmpArr);
      seriesData.forEach(element => {
        legendData.push(element.name);
      });
      return { legendData, seriesData };
    },
    // 冒泡排序
    maopao(arr) {
      var arr1 = arr.slice(0);
      var len = arr1.length;
      for (var i = 0; i < len; i++) {
        for (var j = i + 1; j < len; j++) {
          if (arr1[i].value > arr1[j].value) {
            var tmp = arr1[i];
            arr1[i] = arr1[j];
            arr1[j] = tmp;
          }
        }
      }
      return arr1;
